import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '我的团队' //导航栏文字
			},
			teamList: [{},{},{},{}],
			detail: {}
		}
	},
	onShow() {
		// this.getUserInfo()
		// this.getTeamList()
	},
	methods: {
		getUserInfo() {
			apiUserInfo()
				.then((res) => {
					if (res.code == 0) {
						this.detail = res.data
					}
				})
		},
		getTeamList() {
			apiTeamList()
				.then((res) => {
					if (res.code == 0) {
						this.teamList = res.data
					}
				})
		}
	}
}
